import { computed, ComputedRef } from 'vue';
import { Store, useStore } from 'vuex';
import { IStoreState } from '@/types';
import zeroize from '@/assets/scripts/zeroize';

export default function() {
    const store: Store<IStoreState> = useStore();

    const times: ComputedRef<string> = computed(() => {
        let _h: number = store.state.duration >= 3600000 ? Math.floor(store.state.duration / 3600000) : 0;
        let _m: number = store.state.duration >= 60000 ? Math.floor((store.state.duration - _h * 3600000) / 60000) : 0;
        let _s: number = Math.floor((store.state.duration - _h * 3600000 - _m * 60000) / 1000);
        return `${ _h > 0 ? _h.toString().padStart(2, '0').concat('时') : '' }${ _m.toString().padStart(2, '0') }分${ _s.toString().padStart(2, '0') }秒`;
    });
    const speed: ComputedRef<string> = computed(() => {
        if (store.state.duration === 0) return '--';
        return Math.floor(store.state.accomplish * 60000 / store.state.duration).toString();
    });
    const accuracy: ComputedRef<string> = computed(() => {
        if (store.state.total === 0) return '--';
        let mod: number = store.state.correct * 100 % store.state.total;
        let acc: string = (store.state.correct * 100 / store.state.total).toString();
        return mod === 0 ? acc : zeroize(acc);
    });
    const progress: ComputedRef<string> = computed(() => {
        let mod: number = store.state.accomplish * 100 % store.state.total;
        let pro: string = (store.state.accomplish * 100 / store.state.total).toString();
        return mod === 0 ? pro : zeroize(pro);
    });

    return {
        times,
        speed,
        accuracy,
        progress
    }
}