
    import { defineComponent, PropType, ComputedRef, computed } from 'vue';
    import { ISelectOption } from '@/types';

    interface IParams {
        name: string;
        show: boolean;
        list: Array<ISelectOption>;
    }

    export default defineComponent({
        props: {
            params: {
                type: Object as PropType<IParams>,
                default() {
                    return {}
                }
            }
        },
        setup(props: { params: IParams }, { emit }) {

            const show_text: ComputedRef<string> = computed(() => {
                return props.params.list.find(v => v.selected)?.text || '';
            })

            function onShowTextClick(): void {
                emit('eventHandler', {
                    name: props.params.name
                });
            }

            function onOptionTextClick(_index: number): void {
                emit('eventHandler', {
                    name: props.params.name,
                    index: _index
                });
            }

            return {
                show_text,
                onShowTextClick,
                onOptionTextClick
            }
        }
    })
