<template>
    <div class="menu-unit">
        <div class="show-layer" @click="onShowTextClick">{{ show_text }}</div>
        <transition name="pull-up"> 
            <div class="slide-layer" v-if="params.show">
                <div class="option-text" 
                    v-for="(item, index) in params.list" 
                    :key="'T' + index"
                    :class="{ selected: item.selected }"
                    @click="onOptionTextClick(index)"
                >{{ item.text }}</div>
            </div>
        </transition>
    </div>
</template>

<script lang="ts">
    import { defineComponent, PropType, ComputedRef, computed } from 'vue';
    import { ISelectOption } from '@/types';

    interface IParams {
        name: string;
        show: boolean;
        list: Array<ISelectOption>;
    }

    export default defineComponent({
        props: {
            params: {
                type: Object as PropType<IParams>,
                default() {
                    return {}
                }
            }
        },
        setup(props: { params: IParams }, { emit }) {

            const show_text: ComputedRef<string> = computed(() => {
                return props.params.list.find(v => v.selected)?.text || '';
            })

            function onShowTextClick(): void {
                emit('eventHandler', {
                    name: props.params.name
                });
            }

            function onOptionTextClick(_index: number): void {
                emit('eventHandler', {
                    name: props.params.name,
                    index: _index
                });
            }

            return {
                show_text,
                onShowTextClick,
                onOptionTextClick
            }
        }
    })
</script>

<style lang="scss" scoped>
    .menu-unit {
        height: 0.25rem;
        
        .show-layer {
            position: relative;
            z-index: 10;
            height: 0.25rem;
            background-color: #444;
            font-size: 0.1rem;
            color: #999;
            line-height: 0.25rem;
            text-align: center;
            cursor: pointer;
        }
        .slide-layer {
            background-color: #444;
            transform: translateY(-100%) translateY(-0.25rem);
            -webkit-transform: translateY(-100%) translateY(-0.25rem);
            -o-transform: translateY(-100%) translateY(-0.25rem);
            -ms-transform: translateY(-100%) translateY(-0.25rem);
            -moz-transform: translateY(-100%) translateY(-0.25rem);

            .option-text {
                height: 0.25rem;
                font-size: 0.1rem;
                color: #999;
                line-height: 0.25rem;
                text-align: center;
                padding: 0 0.05rem;
                cursor: pointer;

                &.selected {
                    background-color: #40c7db;
                    font-weight: bold;
                    color: #111;
                }
            }
        }
        .pull-up-enter-active, .pull-up-leave-active {
            transition: all 0.3s ease;
        }
        .pull-up-enter-from, .pull-up-leave-to {
            transform: translateY(-100%);
            -webkit-transform: translateY(-100%);
            -o-transform: translateY(-100%);
            -ms-transform: translateY(-100%);
            -moz-transform: translateY(-100%);
            opacity: 0;
        }
    }
</style>