import { Ref, ref } from 'vue';
import axios from 'axios';
import XLSX from 'xlsx';
import store from '@/store';
import { ESHEET_NAME, EGRADE, ERANGE, ISheetCatalog, ISheetColumn, ISheetCell, IChoiceItem } from '@/types';


interface IBookCatalog {
    [key: string]: XLSX.WorkBook;
}

interface IModuleObject {
    Module: string;
    number: string;
}


let sheet_pool: Array<ISheetCatalog> = [];
const sheet_filter: Ref<ISheetCatalog[]> = ref([]);
const source_pool: Ref<ISheetCell[]> = ref([]);


function onReadLocalExcelTable(): void {
    const table_names: Array<ESHEET_NAME> = [
        ESHEET_NAME.G4V2W,
        ESHEET_NAME.G4V2S
    ];
    let book_catalog: IBookCatalog = {};

    (function _onGetExcelTable(index: number): void {
        let path: string = `excel/${ table_names[index] }`;
        let name: string = table_names[index].match(/.+(?=\.)/)?.[0] || '';
        axios.get(path, { responseType: 'arraybuffer' }).then(res => {
            let _unit8: Uint8Array = new Uint8Array(res.data);
            let _book: XLSX.WorkBook = XLSX.read(_unit8, { type: 'array' });
            book_catalog[name] = _book;
            if (index + 1 < table_names.length) {
                _onGetExcelTable(index + 1);
            } else {
                _onFormatBooks(book_catalog);
            }
        });
    })(0);
}


function _onFormatBooks(book: IBookCatalog): void {
    sheet_pool = [];

    function _onGetModuleOrder(modules: Array<IModuleObject>, unit: string): string {
        for (let i = 0, length = modules.length; i < length; i++) {
            if (RegExp(unit).test(modules[i].number)) {
                return modules[i].Module.match(/(?<=Module)\s*\d+\s*/i)?.[0]?.trim() || '';
            }
        }
        return '';
    }

    function _onFormatSheet(worksheet: XLSX.WorkSheet, _grade: string, _range: string, _module: string, _unit: string): void {
        let sheet_catalog: ISheetCatalog = {
            grade: _grade,
            range: _range,
            module: _module,
            unit: _unit,
            dataset: []
        };
        let sheet_json_array: any = XLSX.utils.sheet_to_json(worksheet, { range: 1 });
        if (_range === 'word') {
            let sheet_column: ISheetColumn = {
                title: '',
                list: []
            };
            for (let i = 0, length = sheet_json_array.length; i < length; i++) {
                sheet_column.list.push({ ...sheet_json_array[i] });
            }
            sheet_catalog.dataset.push(sheet_column);
        } else {
            let obj: { [key: string]: Array<string> } = {};
            for (let j = 0, length = sheet_json_array.length; j < length; j++) {
                for (let k in sheet_json_array[j]) {
                    if (obj[k] === undefined) obj[k] = [];
                    obj[k].push(sheet_json_array[j][k]);
                }
            }
            for (let key in obj) {
                let column: ISheetColumn = {
                    title: '',
                    list: []
                };
                obj[key].forEach(v => {
                    if (RegExp(/^\d+#\s*/).test(v)) {
                        column.title = RegExp(/(?<=\d+#\s*).+/).exec(v)?.[0]?.trim() || '';
                    }
                    if (!RegExp(/(?:#|-)/).test(v)) {
                        column.list.push({ EN: v, CN: '' });
                    }
                })
                sheet_catalog.dataset.push(column);
            }
        }
        sheet_pool.push(sheet_catalog);
    }

    for (let name in book) {
        let _grade: string = RegExp(/(?:grade_\d_volume_\d)/, 'i').exec(name)?.[0] || '';
        let _range: string = RegExp(/(?:word|sentence)/, 'i').exec(name)?.[0] || '';
        let _modules: Array<IModuleObject> = [];
        for (let i = 0, length = book[name].SheetNames.length; i < length; i++) {
            let sheet_name: string = book[name].SheetNames[i];
            if (sheet_name.toLowerCase() === 'module') {
                _modules = XLSX.utils.sheet_to_json(book[name].Sheets[sheet_name], { range: 1 });
            } else if (RegExp(/^unit\s*\d+\s*$/, 'i').test(sheet_name)) {
                let _unit: string = sheet_name.match(/\d+/)?.[0] || '';
                let _module: string = _onGetModuleOrder(_modules, _unit);
                _onFormatSheet(book[name].Sheets[sheet_name], _grade, _range, _module, _unit);
            }
        }
    }

    onSheetFilterPool();
}


function onSheetFilterPool(): void {
    sheet_filter.value = sheet_pool.filter(sheet => {
        return store.state.grade === EGRADE.ALL && (store.state.range === ERANGE.ALL || RegExp(store.state.range).test(sheet.range))
            || RegExp(store.state.grade).test(sheet.grade) && (store.state.range === ERANGE.ALL || RegExp(store.state.range).test(sheet.range));
    });
}


function onSetSourcePool(sheets: Array<ISheetCatalog>, catalog: Array<IChoiceItem>): void {
    let pool: Array<ISheetCell> = [];
    let titles: Array<string> = [];
    catalog.forEach(c => {
        if (c.selected) titles.push(c.text);
    });
    sheets.forEach(s => {
        for (let i = 0, length = s.dataset.length; i < length; i++) {
            if (s.dataset[i].title === '' || titles.some(t => t === s.dataset[i].title)) {
                pool = pool.concat(s.dataset[i].list);
            }
        }
    });
    source_pool.value = pool;
}


export default {
    sheet_filter,
    source_pool,
    onReadLocalExcelTable,
    onSheetFilterPool,
    onSetSourcePool
}