import BH from './behaviors';
import { IStoreState, EGRADE, ERANGE, EPROMPTER, ESORT, ELEVEL } from '@/types';

export default {
    [BH.SET_GRADE](state: IStoreState, grade: EGRADE) {
        state.grade = grade;
    },
    [BH.SET_CATEGORY](state: IStoreState, range: ERANGE) {
        state.range = range;    
    }, 
    [BH.SET_PROMPTER](state: IStoreState, prompter: EPROMPTER) {
        state.prompter = prompter;
    },
    [BH.SET_SORT](state: IStoreState, sort: ESORT) {
        state.sort = sort;
    }, 
    [BH.SET_LEVEL](state: IStoreState, level: ELEVEL) {
        state.level = level;
    },
    [BH.SET_DURATION](state: IStoreState, duration: number) {
        state.duration = duration;
    },
    [BH.SET_CORRECT](state: IStoreState, correct: number) {
        state.correct = correct;
    },
    [BH.SET_TOTAL](state: IStoreState, total: number) {
        state.total = total;
    },
    [BH.SET_ACCOMPLISH](state: IStoreState, accomplish: number) {
        state.accomplish = accomplish;
    },
    [BH.RESET_ACCUMULATOR](state: IStoreState) {
        state.duration = 0;
        state.correct = 0;
        state.total = 0;
        state.accomplish = 0;
    }
}