<template>
    <div class="choice-unit">
        <div class="choice-line" 
            v-for="(item, index) in options.list" 
            :key="'C' + index"
            @click="onChoiceItemClick(index)"
        >
            <div class="choice-text">{{ item.text }}</div>
            <div class="icon-anchor" v-if="item.selected"></div>
        </div>
        <div class="choice-line" @click="onChoiceItemClick()">
            <div class="choice-text">全部</div>
            <div class="icon-anchor" v-if="icon_show"></div>
        </div>
    </div>
</template>

<script lang="ts">
    import { defineComponent, PropType, ComputedRef, computed } from 'vue';
    import { IChoiceItem } from '@/types';

    interface IOptions {
        name: string;
        list: Array<IChoiceItem>;
    }

    export default defineComponent({
        props: {
            options: {
                type: Object as PropType<IOptions>,
                default() {
                    return {}
                }
            }
        },
        setup(props: { options: IOptions }, { emit }) {

            let record: Array<number> = [];

            const icon_show: ComputedRef<boolean> = computed(() => {
                if (props.options.list.length === 0 || props.options.list.some((v: IChoiceItem) => !v.selected)) {
                    return false;
                } else {
                    return true;
                }
            });

            function onChoiceItemClick(_index?: number): void {
                if (_index === undefined && !icon_show.value) {     // 即将全选
                    record = [];    
                    props.options.list.forEach((v, i) => { v.selected && record.push(i); });
                }
                emit('eventHandler', {
                    name: props.options.name,
                    former: icon_show.value ? record : [],
                    index: _index
                });
            }

            return {
                icon_show,
                onChoiceItemClick
            }
        }
    })
</script>

<style lang="scss" scoped>
    .choice-unit {
        max-height: 2.5rem;
        background-color: #666;
        padding: 0.1rem 0.2rem;
        overflow-x: hidden;
        overflow-y: auto;

        scrollbar-width: none;
        -ms-overflow-style: none;
        &::-webkit-scrollbar {
            display: none;
        }
        .choice-line {
            height: 0.15rem;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;

            .choice-text {
                font-size: 0.12rem;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            .icon-anchor {
                $l: 0.1rem;
                position: relative;
                height: $l;

                &::after {
                    content: '';
                    position: absolute;
                    right: -0.15rem;
                    width: $l;
                    height: $l;
                    background: url(~@/assets/icons/ico_check_mark.png) center/contain no-repeat;
                }
            }
        }
    }
</style>