import { reactive, toRefs, onBeforeMount, watch } from 'vue';
import { Store, useStore } from 'vuex';
import BH from '@/store/behaviors';
import { EGRADE, ERANGE, EPROMPTER, ESORT, ELEVEL, ISelectOption, IStoreState } from '@/types';
import character from '@/hooks/characters';


interface IMenuItem {
    text: string;
    value: EGRADE | ERANGE | EPROMPTER | ESORT | ELEVEL;
    selected: boolean;
}

interface IMenuModule {
    name: string;
    show: boolean;
    list: Array<IMenuItem>;
}

interface IMenuData {
    grade: IMenuModule;
    range: IMenuModule;
    prompter: IMenuModule;
    sort: IMenuModule;
    level: IMenuModule;
}

interface IInitItem {
    list: Array<IMenuItem>;
    compare: EGRADE | ERANGE | EPROMPTER | ESORT | ELEVEL;
}


export default function() {
    const store: Store<IStoreState> = useStore();
    const data: IMenuData = reactive({
        grade: {
            name: 'grade',
            show: false,
            list: [
                // {
                //     text: '四年级上学期',
                //     value: EGRADE.G4V1,
                //     selected: true
                // }, 
                {
                    text: '四年级下学期',
                    value: EGRADE.G4V2,
                    selected: false
                }, {
                    text: '全部',
                    value: EGRADE.ALL,
                    selected: false
                }
            ]
        },
        range: {
            name: 'range',
            show: false,
            list: [
                {
                    text: '单词',
                    value: ERANGE.WORD,
                    selected: true
                }, {
                    text: '句子',
                    value: ERANGE.SENTENCE,
                    selected: false
                }, {
                    text: '全部',
                    value: ERANGE.ALL,
                    selected: false
                }
            ]
        },
        prompter: {
            name: 'prompter',
            show: false,
            list: [
                {
                    text: '中文提示',
                    value: EPROMPTER.CHINESE,
                    selected: true
                }, {
                    text: '英文提示',
                    value: EPROMPTER.ENGLISH,
                    selected: false
                }, {
                    text: '朗读提示',
                    value: EPROMPTER.READER,
                    selected: false
                }
            ]
        },
        sort: {
            name: 'sort',
            show: false,
            list: [
                {
                    text: '随机显示',
                    value: ESORT.RANDOM,
                    selected: true
                }, {
                    text: '顺序显示',
                    value: ESORT.ORDER,
                    selected: false
                }
            ]
        },
        level: {
            name: 'level',
            show: false,
            list: [
                {
                    text: '正常',
                    value: ELEVEL.NORMAL,
                    selected: true
                }, {
                    text: '容易',
                    value: ELEVEL.EASY,
                    selected: false
                }, {
                    text: '中等',
                    value: ELEVEL.MIDDLE,
                    selected: false
                }, {
                    text: '困难',
                    value: ELEVEL.DIFFICULT,
                    selected: false
                }
            ]
        }
    });

    function onEventHandler(e: { name: string, index?: number }): void {
        let array: Array<IMenuModule> = [ data.grade, data.range, data.prompter, data.sort, data.level ];
        for (let i = 0, length = array.length; i < length; i++) {
            if (array[i].name === e.name) {
                if (e.index === undefined) {
                    array[i].show = !array[i].show;
                } else {
                    array[i].list.forEach((o: ISelectOption, index: number) => {
                        if (index === e.index) {
                            o.selected = true;
                            switch(e.name) {
                                case data.grade.name: 
                                    store.dispatch(BH.SET_GRADE, o.value);
                                    break;
                                case data.range.name:
                                    store.dispatch(BH.SET_CATEGORY, o.value);
                                    break;
                                case data.prompter.name: 
                                    store.dispatch(BH.SET_PROMPTER, o.value);
                                    break;
                                case data.sort.name: 
                                    store.dispatch(BH.SET_SORT, o.value);
                                    break;
                                case data.level.name:
                                    store.dispatch(BH.SET_LEVEL, o.value);
                                    break;
                            }
                        } else {
                            o.selected = false;
                        }
                    });
                    array[i].show = false;
                }
            } else {
                array[i].show = false;
            }
        }
    }

    function onOutsideClick(): void {
        let array: Array<IMenuModule> = [ data.grade, data.range, data.prompter, data.sort, data.level ];
        array.forEach(m => { m.show = false; });
    }

    function _onInit(): void {
        let array: Array<IInitItem> = [
            {
                list: data.grade.list,
                compare: store.state.grade
            }, {
                list: data.range.list,
                compare: store.state.range
            }, {
                list: data.prompter.list,
                compare: store.state.prompter
            }, {
                list: data.sort.list,
                compare: store.state.sort
            }, {
                list: data.level.list,
                compare: store.state.level
            }
        ];
        for (let i = 0, length = array.length; i < length; i++) {
            for (let k = 0, len = array[i].list.length; k < len; k++) {
                if (array[i].list[k].value === array[i].compare) {
                    array[i].list[k].selected = true;
                } else {
                    array[i].list[k].selected = false;
                }
            }
        }
    }

    watch([() => store.state.grade, () => store.state.range], () => {
        character.onSheetFilterPool();
    })

    onBeforeMount(() => {
        _onInit();
    })

    return {
        ...toRefs(data),
        onEventHandler,
        onOutsideClick
    }
}