import { App, DirectiveBinding } from 'vue';

interface IElement extends HTMLElement {
    _zOutSideClick?: (e: MouseEvent) => void;
}

export default {
    install(app: App): void {
        app.directive('outsideclick', {
            mounted(el: IElement, binding: DirectiveBinding) {
                if (typeof binding.value === 'function') {
                    function handler(e: MouseEvent): void {
                        if (!el.contains(<HTMLElement>e.target)) binding.value();
                    }
                    el._zOutSideClick = handler;
                    document.addEventListener('click', handler);
                }                
            }, 
            beforeUnmount(el: IElement) {
                if (el._zOutSideClick) {
                    document.removeEventListener('click', el._zOutSideClick);
                    delete el._zOutSideClick;
                }
            }
        });
    }
}