import { Commit } from 'vuex';
import { IStoreState, EGRADE, ERANGE, EPROMPTER, ESORT, ELEVEL } from '../types';
import BH from './behaviors';

interface ICtx {
    commit: Commit;
    state: IStoreState;
}

export default {
    [BH.SET_GRADE]({ commit }: ICtx, grade: EGRADE) {
        commit(BH.SET_GRADE, grade);
    },
    [BH.SET_CATEGORY]({ commit }: ICtx, range: ERANGE) {
        commit(BH.SET_CATEGORY, range);
    },
    [BH.SET_PROMPTER]({ commit }: ICtx, prompter: EPROMPTER) {
        commit(BH.SET_PROMPTER, prompter);
    },
    [BH.SET_SORT]({ commit }: ICtx, sort: ESORT) {
        commit(BH.SET_SORT, sort);
    },
    [BH.SET_LEVEL]({ commit }: ICtx, level: ELEVEL) {
        commit(BH.SET_LEVEL, level);
    },
    [BH.SET_DURATION]({ commit }: ICtx, duration: number) {
        commit(BH.SET_DURATION, duration);
    },
    [BH.SET_CORRECT]({ commit }: ICtx, correct: number) {
        commit(BH.SET_CORRECT, correct);
    },
    [BH.SET_TOTAL]({ commit }: ICtx, total: number) {
        commit(BH.SET_TOTAL, total);
    },
    [BH.SET_ACCOMPLISH]({ commit }: ICtx, accomplish: number) {
        commit(BH.SET_ACCOMPLISH, accomplish);
    },
    [BH.RESET_ACCUMULATOR]({ commit }: ICtx) {
        commit(BH.RESET_ACCUMULATOR);
    }
}