import { reactive, toRefs, watch, onBeforeMount, onBeforeUnmount } from 'vue';
import { Store, useStore } from 'vuex';
import { EPROMPTER, ISheetCell, IStoreState, ESORT, ELEVEL } from '@/types';
import character from '@/hooks/characters';
import BH from '@/store/behaviors';


enum EPROMPTER_TYPE {
    CHINESE = 'chinese',
    ENGLISH = 'english',
    EMPTY = 'empty'
}
enum EINPUT_COLOR {
    IDLE = '',
    WARN = 'warn',
    CORRECT = 'correct'
}
interface IDictationData {
    tips: string;
    content: string;
    content_color: EINPUT_COLOR;
    prompter_step: EPROMPTER_TYPE;
    window_show: boolean;
}


export default function() {
    const store: Store<IStoreState> = useStore();

    let pool: Array<ISheetCell> = [];
    let cell: ISheetCell | undefined = undefined;
    let cell_english: string = '';
    let counter: number = 0;
    let timeoutId: number | null = null;
    let intervalId: number | null = null;
    let delay_counter: number = 0;
    let delay_level: number = 0;
    let start_stamp: number = 0;
    let start_valve: boolean = false;
    const audio: HTMLAudioElement = new Audio();

    const data: IDictationData = reactive({
        tips: '',
        content: '',
        content_color: EINPUT_COLOR.IDLE,
        prompter_step: EPROMPTER_TYPE.EMPTY,
        window_show: false
    });

    // 播放读音
    function _onReadAloud(): void {
        counter++;
        if (counter <= 3) {
            counter === 1 && (audio.src = `http://dict.youdao.com/dictvoice?type=1&audio=${ cell?.EN }`);
            audio.play();
        }
    }

    // 播放结束后重复播放
    function _onAudioPlayEnd(): void {
        timeoutId = setTimeout(() => {
            _onReadAloud();
            timeoutId = null;
        }, 1000);
    }

    // 点击播放读音
    function onHornClick(): void {
        if (timeoutId !== null) {
            clearTimeout(timeoutId);
            timeoutId = null;
        }
        counter = 0;
        if (audio.played) audio.pause(); 
        if (cell?.EN && cell.EN.length > 0) _onReadAloud();
    }

    // 切换提示内容
    function onPromptClick(): void {
        if (data.prompter_step === EPROMPTER_TYPE.ENGLISH) {
            data.tips = cell?.EN || '';
            if (cell?.CN) {
                data.prompter_step = EPROMPTER_TYPE.CHINESE;
            } else {
                data.prompter_step = EPROMPTER_TYPE.EMPTY;
            }
        } else if (data.prompter_step === EPROMPTER_TYPE.CHINESE) {
            data.tips = cell?.CN || '';
            if (cell?.EN && store.state.prompter !== EPROMPTER.READER) {
                data.prompter_step = EPROMPTER_TYPE.ENGLISH;
            } else {
                data.prompter_step = EPROMPTER_TYPE.EMPTY;
            }
        } else if (data.prompter_step === EPROMPTER_TYPE.EMPTY) {
            data.tips = '';
            if (cell?.EN) {
                data.prompter_step = EPROMPTER_TYPE.ENGLISH;
            } else if (cell?.CN) {
                data.prompter_step = EPROMPTER_TYPE.CHINESE;
            } else {
                data.prompter_step = EPROMPTER_TYPE.EMPTY;
            }
        }
    }

    // 输入中...
    function onInputValue(): void {
        !start_valve && (start_valve = true);
        data.content = data.content.replace(/^\s+|\b\s+\b|\s+$/g, ' ');
        let _content: string = data.content.replace(/\b\s+\b/g, ' ').trim();
        if (_content.length < cell_english.length && cell_english.substr(0, _content.length) === _content) {
            data.content_color = EINPUT_COLOR.IDLE;
        } else if (cell_english === _content) {
            data.content_color = EINPUT_COLOR.CORRECT;
        } else {
            data.content_color = EINPUT_COLOR.WARN;
        }
    }

    // 输入结束 
    function onEnterClick(): void {
        if (timeoutId !== null) {
            clearTimeout(timeoutId);
            timeoutId = null;
        }
        if (audio.played) audio.pause();
        let _input: string = data.content.replace(/\b\s+\b/g, ' ').trim();
        let cell_length: number = cell_english.replace(/\b\s+\b/g, '').length;
        _input === cell_english && store.dispatch(BH.SET_CORRECT, store.state.correct + cell_length);
        store.dispatch(BH.SET_ACCOMPLISH, store.state.accomplish + cell_length);
        _onPracticeCell();
    }

    // 提示内容
    function _onPrompter(): void {
        data.tips = store.state.prompter === EPROMPTER.CHINESE ? cell?.CN || '' : 
            store.state.prompter === EPROMPTER.ENGLISH ? cell?.EN || '' : '';
        if (cell?.EN && (store.state.prompter === EPROMPTER.CHINESE || store.state.prompter === EPROMPTER.READER)) {
            data.prompter_step = EPROMPTER_TYPE.ENGLISH;
        } else if (cell?.CN && (store.state.prompter === EPROMPTER.ENGLISH || store.state.prompter === EPROMPTER.READER)) {
            data.prompter_step = EPROMPTER_TYPE.CHINESE;
        } else {
            data.prompter_step = EPROMPTER_TYPE.EMPTY;
        }
    }

    // 获取练习单词或句子
    function _onPracticeCell(): void {
        if (pool.length === 0) {
            start_valve = false;
            data.window_show = true;
        } else {
            if (store.state.sort === ESORT.ORDER) {
                cell = pool.shift();
            } else {
                let index: number = Math.round(Math.random() * pool.length) - 1;
                cell = pool.splice(index, 1)[0];
            }
            cell_english = cell?.EN.replace(/\b\s+\b/g, ' ').trim() || '';
            data.content = '';
            data.content_color = EINPUT_COLOR.IDLE;
            delay_counter = 0;
            switch(store.state.level) {
                case ELEVEL.EASY:
                    delay_level = 3 * cell_english.length;
                    break;
                case ELEVEL.MIDDLE:
                    delay_level = 1.5 * cell_english.length;
                    break;
                case ELEVEL.DIFFICULT:
                    delay_level = 0.5 * cell_english.length;
                    break;
            }
            _onPrompter();
            store.state.prompter === EPROMPTER.READER && cell?.EN && onHornClick(); // 模拟点击播放读音
        }
    }

    /** 计时 */
    function _onIntervalProcess(): void {
        if (intervalId !== null) {
            clearInterval(intervalId);
            intervalId = null;
        }
        store.dispatch(BH.RESET_ACCUMULATOR);
        delay_counter = 0;
        start_valve = false;
        start_stamp = (new Date()).getTime();
        intervalId = setInterval(() => {
            if (start_valve) {
                let diff: number = (new Date()).getTime() - start_stamp;
                store.dispatch(BH.SET_DURATION, diff);
                if (store.state.level !== ELEVEL.NORMAL) {
                    delay_counter++;
                    delay_counter >= delay_level && onEnterClick();   // 延时结束，模拟输入结束
                }
            } else {
                start_stamp = (new Date()).getTime();
            }
        }, 1000);
    }

    function onPopupWindow(form: string): void {
        data.window_show = false;
        if (form === 'again') {
            _onInit();
        } else if (form === 'reset') {
            console.log('reset')
        }
    }

    function _onInit(): void {
        _onIntervalProcess();
        pool = character.source_pool.value.map(v => v);
        let total: number = 0;
        for (let i = 0, length = pool.length; i < length; i++) {
            let EN: string = pool[i].EN.replace(/\b\s+\b/g, '').trim();
            total += EN.length;
        }
        store.dispatch(BH.SET_TOTAL, total);
        _onPracticeCell();
    }

    // 监听资源池改变
    watch(() => character.source_pool.value, () => {
        _onInit();
    })

    watch(() => store.state.prompter, () => {
        _onPrompter();
    })

    onBeforeMount(() => {
        audio.addEventListener('ended', _onAudioPlayEnd);
    })

    onBeforeUnmount(() => {
        audio.removeEventListener('ended', _onAudioPlayEnd);
        if (timeoutId !== null) {
            clearTimeout(timeoutId);
            timeoutId = null;
        }
        if (intervalId !== null) {
            clearInterval(intervalId);
            intervalId = null;
        }
    })

    return {
        ...toRefs(data),
        onHornClick,
        onPromptClick,
        onInputValue,
        onEnterClick,
        onPopupWindow
    }
}