import { reactive, toRefs, watch } from 'vue';
import { IChoiceItem, ISheetCatalog } from '@/types';
import character from '@/hooks/characters';


interface ICatalog {
    name: string;
    list: Array<IChoiceItem>;
}

interface IChoiceData {
    module_catalog: ICatalog;
    unit_catalog: ICatalog;
    sentence_catalog: ICatalog;
}


export default function() {
    let selected_sheets: Array<ISheetCatalog> = [];
    const data: IChoiceData = reactive({
        module_catalog: {
            name: 'model',
            list: []
        },
        unit_catalog: {
            name: 'unit',
            list: []
        },
        sentence_catalog: {
            name: 'sentence',
            list: []
        }
    })

    // 菜单点击事件
    function onEventHandler(e: { name: string, former: Array<number>, index?: number }): void {
        let array: Array<ICatalog> = [ data.module_catalog, data.unit_catalog, data.sentence_catalog ];
        for (let i = 0, length = array.length; i < length; i++) {
            if (array[i].name === e.name) {
                array[i].list.forEach((item: IChoiceItem, k: number) => {
                    if (e.index === undefined) {
                        if (e.former.length === 0 || e.former.some(index => index === k)) {
                            item.selected = true
                        } else {
                            item.selected = false;
                        }
                    } else if (e.index === k && (array[i].list.filter(v => v.selected).length > 1 || !item.selected)) {
                        item.selected = !item.selected;
                    }
                });
                switch(e.name) {
                    case data.module_catalog.name: 
                        _onRelationChoice(true);
                        break;
                    case data.unit_catalog.name: 
                        _onRelationChoice(false);
                        break;
                    case data.sentence_catalog.name: 
                        character.onSetSourcePool(selected_sheets, data.sentence_catalog.list);
                        break;
                }
                break;
            }
        }
    }

    /**
     * @param direction 关联方向 true:模块关联单元  false:单元关联模块
     */
    function _onRelationChoice(direction: boolean = true): void {
        let main: Array<string> = [];
        let main_list: Array<IChoiceItem> = direction ? data.module_catalog.list : data.unit_catalog.list;
        main_list.forEach(m => {
            if (m.selected) main.push(m.serial);
        });
        if (main.length > 0) {
            let m_reg: string = `^(?:${ main.join('|') })$`;
            let deputy: Array<string> = [];
            for (let i = 0, length = character.sheet_filter.value.length; i < length; i++) {
                if (RegExp(m_reg).test(direction ? character.sheet_filter.value[i].module : character.sheet_filter.value[i].unit)) {
                    deputy.push(direction ? character.sheet_filter.value[i].unit : character.sheet_filter.value[i].module);
                }
            }
            if (deputy.length > 0) {
                let d_reg: string = `^(?:${ deputy.join('|') })$`;
                let deputy_list: Array<IChoiceItem> = direction ? data.unit_catalog.list : data.module_catalog.list;
                deputy_list.forEach(d => {
                    if (RegExp(d_reg).test(d.serial)) {
                        d.selected = true;
                    } else {
                        d.selected = false;
                    }
                });
            }
        }
        _onSentenceChoiceMenu();
    }

    /** 句子标题菜单 */
    function _onSentenceChoiceMenu(): void {
        data.sentence_catalog.list = [];
        let units: Array<string> = [];
        data.unit_catalog.list.forEach(u => {
            if (u.selected) units.push(u.serial);
        });
        if (units.length > 0) {
            let u_reg: string = `^(?:${ units.join('|') })$`;
            selected_sheets = character.sheet_filter.value.filter(s => RegExp(u_reg).test(s.unit));
            for (let i = 0, length = selected_sheets.length; i < length; i++) {
                selected_sheets[i].dataset.forEach(d => {
                    if (d.title.length > 0 && !data.sentence_catalog.list.some(s => s.text.toLowerCase() === d.title.toLowerCase())) {
                        let _sentence: IChoiceItem = {
                            serial: '',
                            text: d.title,
                            selected: data.sentence_catalog.list.length === 0 ? true : false
                        };
                        data.sentence_catalog.list.push(_sentence);
                    }
                });
            }
            character.onSetSourcePool(selected_sheets, data.sentence_catalog.list);
        }
    }

    /** 模块/单元列表 */
    function _onInitialization(sheets: Array<ISheetCatalog>): void {
        data.module_catalog.list = [];
        data.unit_catalog.list = [];
        for(let i = 0, length = sheets.length; i < length; i++) {
            if (!data.module_catalog.list.some(v => v.serial === sheets[i].module)) {
                let _module: IChoiceItem = {
                    serial: sheets[i].module,
                    text: `Module ${ sheets[i].module }`,
                    selected: data.module_catalog.list.length === 0 ? true : false
                };
                data.module_catalog.list.push(_module);
            }
            if (!data.unit_catalog.list.some(v => v.serial === sheets[i].unit)) {
                let _unit: IChoiceItem = {
                    serial: sheets[i].unit,
                    text: `Unit ${ sheets[i].unit }`,
                    selected: false
                };
                data.unit_catalog.list.push(_unit);
            }
        }
        _onRelationChoice(true);
    }

    watch(() => character.sheet_filter.value, new_value => {
        _onInitialization(new_value);
    })

    return {
        ...toRefs(data),
        onEventHandler
    }
}