<template>
    <div class="popup-window">
        <transition name="window">
            <div class="settlement-layer" v-if="show">
                <div class="emoticon">~O(∩_∩)O~</div>
                <div class="text">你已顺利完成本次练习任务！</div>
                <div class="try-again" @click="onTryAgainClick">再来一次</div>
                <!-- <div class="reset-text" @click="onResetClick">重置设置</div> -->
            </div>
        </transition>
    </div>
</template>

<script lang="ts">
    import { defineComponent, reactive, toRefs, onMounted } from 'vue';

    export default defineComponent({
        setup(_, { emit }) {
            let data = reactive({
                show: false
            });

            function onTryAgainClick(): void {
                data.show = false;
                setTimeout(() => { emit('eventHandler', 'again'); }, 500);
            }

            function onResetClick(): void {
                data.show = false;
                setTimeout(() => { emit('eventHandler', 'reset'); }, 500);
            }

            onMounted(() => {
                setTimeout(() => { data.show = true; }, 100);
            })

            return {
                ...toRefs(data),
                onTryAgainClick,
                onResetClick
            }
        }
    })
</script>

<style lang="scss" scoped>
    .popup-window {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(17, 17, 17, 0.3);
        display: flex;
        justify-content: center;
        align-items: center;

        .settlement-layer {
            position: relative;
            width: 4rem;
            // height: 1.85rem;
            background-color: rgba(17, 17, 17, 0.9);
            border-radius: 0.08rem;
            border: 1px solid #888;
            padding: 0.2rem;

            .emoticon, .text {
                font-family: 'Times New Roman', Times, serif;
                font-size: 0.2rem;
                color: #99cc33;
                text-align: center;
            }
            .text {
                font-size: 0.18rem;
                margin-top: 0.1rem;
            }
            .try-again {
                width: 1.2rem;
                height: 0.4rem;
                border-radius: 0.05rem;
                border: 1px solid #666;
                background-color: rgba(51, 51, 51, 0.5);
                font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
                font-size: 0.14rem;
                color: #ccc;
                text-align: center;
                line-height: 0.4rem;
                margin: 0.3rem auto 0;
                cursor: pointer;

                &:hover {
                    filter: brightness(1.5);
                }
            }
            .reset-text {
                position: absolute;
                right: 0.2rem;
                bottom: 0.1rem;
                font-size: 0.12rem;
                color: #999;
                border-bottom: 1px solid #999;
                cursor: pointer;

                &:hover {
                    filter: brightness(1.5);
                }
            }
        }
        .window-enter-active, .window-leave-active {
            transition: all 0.3s ease;
        }
        .window-enter-from, .window-leave-to {
            transform: scale(0.5);
            opacity: 0;
        }
    }
</style>