
    import { defineComponent, reactive, toRefs, onMounted } from 'vue';

    export default defineComponent({
        setup(_, { emit }) {
            let data = reactive({
                show: false
            });

            function onTryAgainClick(): void {
                data.show = false;
                setTimeout(() => { emit('eventHandler', 'again'); }, 500);
            }

            function onResetClick(): void {
                data.show = false;
                setTimeout(() => { emit('eventHandler', 'reset'); }, 500);
            }

            onMounted(() => {
                setTimeout(() => { data.show = true; }, 100);
            })

            return {
                ...toRefs(data),
                onTryAgainClick,
                onResetClick
            }
        }
    })
